import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import "./StandardOutputEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import standardOutputApi from "api/common/standardOutput";
import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { isErrors } from "utils/utils";

const initValues = {
  ten: "",
  mucDo: 0,
};

const StandardOutputEdit = function ({
  values,
  handleChange,
  resetForm,
  errors,
}) {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);

  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await standardOutputApi.getById(id);
      if (res) {
        values.mucDo = res.mucDo;
        values.ten = res.ten;
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    }
    setIsLoading(false);
  };

  const resetNoti = () => {
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
  }

  const handleEditOnClick = async function (e) {
    e.preventDefault();
    resetNoti();
    try {
      if (isUpdatePage) {
        const res = await standardOutputApi.putById(id, values);
        if (res) {
          if (res.status === 201 || res.status === 200 || String(res?.message) === 'UPDATE_CHUANDAURA_SUCCESSFULLY') {
            toast.success(t("admin.UPDATE_SUCCESS"));
            updateForm(id);
          } else {
            setNotification({
              color: "danger",
              isOpen: true,
              messages: [res.error],
            });
          }
        }
      } else {
        const res = await standardOutputApi.post(values);
        if (res) {
          if (res.status === 201 || res.status === 200 ) {
            toast.success(t("admin.ADD_SUCCESS"));
          } else {
            setNotification({
              color: "danger",
              isOpen: true,
              messages: [res.error],
            });
          }
        }
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("MAJOR_STANDARD_OUTPUT_EXIST"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    resetForm();
    resetNoti();
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    resetNoti();
    try {
      const res = await standardOutputApi.deleteById(id);
      if (res) {
        if (res.status === 200) {
          toast.success(t("admin.common.DELETED"));
          history.push(RoutesString.ADMIN_STANDARD_OUTPUT_LIST)
        } else {
          setNotification({
            color: "danger",
            isOpen: true,
            messages: [t("ERROR")],
          });
        }
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.standard_output_edit.UPDATE")
            : t("admin.standard_output_edit.ADD")
        }
        items={[
          {
            href: RoutesString.ADMIN_STANDARD_OUTPUT_LIST,
            title: t("admin.standard_output_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          isUpdatePage
            ? t("admin.standard_output_edit.UPDATE")
            : t("admin.standard_output_edit.ADD")
        }
      />

      <div className="page-content">
        <Form>
          <Notification
            color={notification.color}
            isOpen={notification.isOpen}
            messages={notification.messages}
          />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.standard_output_list.NAME")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ten"
                  placeholder={t("admin.standard_output_edit.NAME_PLACEHOLDER")}
                  value={values.ten}
                  onChange={handleChange}
                  // invalid={errors.ten}
                />
              </InputGroup>
              {errors.ten && <FormText color="danger">{errors.ten}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="value" className="col-lg-3">
              {t("admin.standard_output_list.LEVEL")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaUserAlt />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="mucDo"
                  placeholder={t(
                    "admin.standard_output_edit.LEVEL_PLACEHOLDER"
                  )}
                  value={values.mucDo}
                  onChange={handleChange}
                  // invalid={errors.mucDo}
                />
              </InputGroup>
              {errors.mucDo && (
                <FormText color="danger">{errors.mucDo}</FormText>
              )}
            </div>
          </FormGroup>{" "}

          <br />
          <FormGroup>
            <Button color="success" onClick={handleEditOnClick} disabled={isErrors(errors)}>
              {isUpdatePage ? (
                <>
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </>
              ) : (
                <>
                  <FaPlus /> {t("admin.common.ADD")}
                </>
              )}
            </Button>
            &emsp;
            {isUpdatePage && (
              <>
                <Button onClick={handleRefreshOnClick}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
                &emsp;
                <Button color="danger" onClick={handleDeleteOnClick}>
                  <FaTrash /> {t("admin.common.DELETE")}
                </Button>
              </>
            )}
          </FormGroup>
        </Form>
      </div>
    </div>
  );
};

const StandardOutputEditFormikForm = withFormik({
  mapPropsToValues() {
    return { ...initValues };
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    mucDo: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .min(0, i18n.t("admin.POSITIVE_WARNING")),
    ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
  }),
})(StandardOutputEdit);

export default StandardOutputEditFormikForm;